#image-outside {
    padding-top:5vw;
    padding-left: 5vw;
    padding-right: 5vw;
}
#image-title {
    padding-bottom: 2vh;
}


.image-container{  
    display:flex;
    flex-direction: row;
    max-width: 90vw;
    padding-top: 2vw;
    padding-bottom: 2vw;
    
    justify-content: center;
    align-items: center;
    text-align: center;
    
}
.image-container img{
    border:1px transparent solid;
    border-top-left-radius: 5vw;
    border-top-right-radius: 5vw;
    border-bottom-left-radius: 5vw;
    border-bottom-right-radius: 5vw;
    height: fit-content;
}

  